@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf");
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.ttf");
  font-weight: 700;
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: hsl(235, 18%, 26%);
  line-height: 1;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

img {
  display: block;
  max-width: 100%;
}

p {
  margin: 0;
  line-height: 1.5;
  color: hsl(234, 29%, 20%);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(100%, 85rem);
  margin: 0 auto;
}
.container--success {
  width: min(100%, 35rem);
}

.main .d-none {
  display: none;
}

/* FOOTER */
.attribution {
  font-size: 1rem;
  text-align: center;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .attribution {
    font-size: 1.2rem;
    position: absolute;
    bottom: 0;
  }
}
.attribution .attribution__text {
  color: hsl(0, 0%, 100%);
}
.attribution .attribution__link {
  color: hsl(4, 100%, 67%);
}

.sign-up {
  background-color: hsl(0, 0%, 100%);
}
@media (min-width: 768px) {
  .sign-up {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    padding: 1.5rem;
    border-radius: 2rem;
    gap: 2rem;
    overflow: hidden;
  }
}
.sign-up__image {
  width: 100%;
}
.sign-up__content {
  padding: 2rem;
}
@media (min-width: 768px) {
  .sign-up__content {
    padding: 3rem;
  }
}
.sign-up__heading {
  font-size: 4rem;
  margin: 2rem 0;
  color: hsl(234, 29%, 20%);
}
@media (min-width: 768px) {
  .sign-up__heading {
    font-size: 5.5rem;
  }
}

.list-benefits {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1fr, 1fr);
  margin: 2.5rem 0;
}

.benefit {
  display: flex;
  gap: 1.5rem;
  align-items: flex-start;
}

.form__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form__label, .form__error {
  font-weight: bold;
  font-size: 1.2rem;
  margin: 0;
}
.form__label {
  color: hsl(234, 29%, 20%);
}
.form__error {
  color: #bd0d00;
}
.form__input {
  outline: none;
  border: 1px solid hsl(231, 7%, 60%);
  border-radius: 1rem;
  padding: 1.5rem 2.5rem;
  margin: 1rem 0;
  width: 100%;
}
.form__input:focus-visible {
  outline: none;
  border: 1px solid hsl(234, 29%, 20%);
}
.form__input--error {
  border: 1px solid hsl(4, 100%, 67%);
  color: #bd0d00;
  background-color: #fff1f0;
}
.form__input--error:focus-visible {
  border: 1px solid hsl(4, 100%, 67%);
}
.form__btn {
  cursor: pointer;
  background-color: hsl(234, 29%, 20%);
  padding: 1.5rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: hsl(0, 0%, 100%);
  text-decoration: none;
  border: none;
  transition: background-color 0.3s;
  margin: 1rem 0;
}
.form__btn:hover {
  background-image: linear-gradient(to right, #FF537A 0%, hsl(4, 100%, 67%) 60%, hsl(4, 100%, 67%) 100%);
  box-shadow: 0px 7px 25px 1px rgba(255, 98, 87, 0.72);
}

.success-message {
  background-color: hsl(0, 0%, 100%);
  padding: 2rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15rem;
}
@media (min-width: 768px) {
  .success-message {
    height: auto;
    display: block;
    border-radius: 2rem;
    padding: 4rem;
  }
}
.success-message__content {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1fr, 1fr);
}
.success-message__image {
  width: 5rem;
}
.success-message__heading {
  font-size: 3.5rem;
  line-height: 1;
  font-weight: bold;
}
@media (min-width: 768px) {
  .success-message__heading {
    font-size: 3.8rem;
  }
}
.success-message__description {
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .success-message__description {
    margin-bottom: 3rem;
    font-size: 1.2rem;
  }
}
.success-message__email {
  font-weight: bold;
}
.success-message__btn {
  cursor: pointer;
  background-color: hsl(234, 29%, 20%);
  padding: 1.5rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: hsl(0, 0%, 100%);
  text-decoration: none;
  border: none;
  transition: background-color 0.3s;
}
.success-message__btn:hover {
  background-image: linear-gradient(to right, #FF537A 0%, hsl(4, 100%, 67%) 60%, hsl(4, 100%, 67%) 100%);
  box-shadow: 0px 7px 25px 1px rgba(255, 98, 87, 0.72);
}